                                    <div class="anchorName">{{fixedPromotionClassItem.actSlogan}}</div>
                                    <div class="anchorDesc">{{fixedPromotionClassItem.actTag}}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
            <img class="circle" v-show="isShowCircle" src="@/assets/images/mall/mall_icon_add.png" alt="">
        </transition>

        <div class="maskLayer" v-show="isShowMaskBox" @click.stop="hideMaskBox"></div>
        <div class="maskBox" v-show="isShowMaskBox" @click.stop="hideMaskBox">
            <div class="maskBoxContent" :style="{paddingBottom:this.$footBarHeight+'px'}" @click.stop="">
                <div class="maskBoxContxt">
                    <div class="couponBox">
                        <div class="couponTxt">优惠券</div>
                        <div class="couponList">
                            <div class="couponItem"
                                 v-for="(pickupItem,pickupIndex) in pickupList" :key="'pickupItem'+pickupIndex"
                                 :class="(pickupItem.isFinished==1&&pickupItem.isPicked!=1)?'grayCouponInfo':(pickupItem.isPicked==1?'carCouponInfo':'')">
                                <div class="couponInfo">
                                    <div class="couponLeft">
                                        <div class="couponMoney">
                                            <span class="couponMoneyIcon">￥</span>
                                            <span class="couponMoneyNum">{{pickupItem.couponTotal}}</span>
                                        </div>
                                        <div class="couponShopInfo">
                                            <div class="couponShop">{{pickupItem.name}}</div>
                                            <div class="couponDesc" v-if="pickupItem.effType == '1'">{{pickupItem.effBeginDay}}-{{pickupItem.effEndDay}}</div>
                                            <div class="couponDesc" v-if="pickupItem.effType == '2'&&pickupItem.effDays == 1">领取后当天有效</div>
                                            <div class="couponDesc" v-if="pickupItem.effType == '2'&&pickupItem.effDays != 1">领取后{{pickupItem.effDays}}天内有效</div>
                                        </div>
                                    </div>
                                    <div class="receiveBtn" v-if="pickupItem.isPicked==0&&pickupItem.isFinished==0">立即领取</div>
                                    <div class="receiveBtn" v-if="pickupItem.isPicked==1">已领取</div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="reductionBox">
                        <div class="reductionTitle">
                            <div class="reductionTxt">满减活动</div>
                            <div class="goToFullBox">
                                <div class="goToFullTxt">去逛逛</div>
                                <img src="@/assets/images/mall/mall_icon_more.png" alt="">
                            </div>
                        </div>
                        <div class="reductionList">
                            <div class="reductionItem">
                                <div class="reductionIcon">满减</div>
                                <div class="reductionCon">好丽友满100减10</div>
                            </div>
                            <div class="reductionItem">
                                <div class="reductionIcon">满减</div>
                                <div class="reductionCon">德芙满100减5</div>
                            </div>
                        </div>
                    </div>
                    <div class="packBox">
                        <div class="packTitle">
                            <div class="packTxt">打包促销</div>
                            <div class="goToPackBox">
                                <div class="goToPackTxt">去逛逛</div>
                                <img src="@/assets/images/mall/mall_icon_more.png" alt="">
                            </div>
                        </div>
                        <div class="packList">
                            <div class="packItem">
                                <div class="packIcon">打包</div>
                                <div class="packCon">好丽友100元5件</div>
                            </div>
                            <div class="packItem">
                                <div class="packIcon">打包</div>
                                <div class="packCon">德芙200元4件</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
