<script>
    import {shopPage} from './shopPage'
    export default {
        name: 'shopPage',
        mixins:[shopPage]
    }
</script>

<style scoped lang="scss">
    .maskLayer{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background: rgba(0, 0, 0, 0.5);
        z-index: 10;
    }
    .maskBox {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        z-index: 15;
        .maskBoxContent {
            width: 100%;
            height: 395px;
            position: absolute;
            bottom: 0;
            background: #fff;
            border-radius: 10px 10px 0px 0px;

            .maskBoxContxt {
                width: 100%;
                height: 100%;
                box-sizing: border-box;
                padding: 15px 25px 15px 15px;
                overflow: scroll;

                .couponBox {
                    .couponTxt {
                        width: 42px;
                        height: 20px;
                        line-height: 20px;
                        font-family: PingFangSC-Medium;
                        font-size: 14px;
                        font-weight: 600;
                        color: #333333;
                        letter-spacing: 0;
                    }

                    .couponList {
                        margin-top: 10px;

                        .couponItem {
                            width: 335px;
                            height: 81.5px;
                            background: #FFFFFF;
                            border-radius: 4px;
                            box-sizing: border-box;
                            padding: 4px;
                            margin-bottom: 10px;
                            background: url(../../assets/images/mall/mall_bg_coupon_unaccalimed.png) no-repeat;
                            -webkit-background-size: 100% 100%;
                            background-size: 100% 100%;
                            box-sizing: border-box;

                            .couponInfo {
                                width: 100%;
                                height: 100%;
                                border-radius: 2px;
                                box-sizing: border-box;
                                display: flex;
                                justify-content: space-between;
                                align-items: center;
                                padding: 0 14px 0 0;

                                .couponLeft {
                                    width: 250px;
                                    height: 100%;
                                    display: flex;
                                    align-items: center;

                                    .couponMoney {
                                        width: 95px;
                                        text-align: center;

                                        .couponMoneyIcon {
                                            font-family: PingFangSC-Semibold;
                                            font-size: 14px;
                                            color: #FFFFFF;
                                        }

                                        .couponMoneyNum {
                                            display: inline-block;
                                            font-family: PingFangSC-Semibold;
                                            font-size: 40px;
                                            color: #FFFFFF;
                                        }
                                    }

                                    .couponShopInfo {
                                        margin-left: 12px;

                                        .couponShop {
                                            width: 130px;
                                            overflow: hidden;
                                            text-overflow: ellipsis;
                                            white-space: nowrap;
                                            font-family: PingFangSC-Medium;
                                            font-size: 14px;
                                            color: #FFFFFF;
